body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.result {
    margin-left: 20px;
    margin-right: 50px;
    padding-top: 20px;
    border-top: 2px solid #ddd;
}

.result h3,
.result h4 {
    font-weight: normal;
}

.result em {
    font-weight: bold;
    color: #1242b6;
}
.App {
    margin-left: 50px;
}

.App h1 {
    color: #1242b6;
    margin-left: 20px;
}

.App input,
.App button {
    margin: 20px;
    padding: 10px 20px;
    border: 2px solid gray;
    background-color: #fff;
    transition: .3s;
}


.App button {
    cursor: pointer;
}

.App button:hover {
    background-color: #000;
    color: white;
}
